import { EDITOR } from 'cc/env';
import { director } from 'cc';
import { Asset, Node, Prefab, Sprite, SpriteFrame, _decorator } from 'cc';
import * as EditorExtends from 'editor-extends';

if (EDITOR) {
  const { Menu, Path } = EditorExtends;

  // 注册到编辑器菜单
  Menu.addMenuItem('Project/生成麻将Prefab', async () => {
    await generateMahjongPrefabs();
  });

  async function generateMahjongPrefabs() {
    // 配置参数
    const config = {
      basePath: 'Miaojiang/miaojiang_bai',   // 底图路径
      faceDir: 'Tiles',                      // 牌面目录
      outputDir: 'Prefabs/Mahjong',          // 输出目录
      types: ['wan', 'tong', 'tiao', 'feng', 'jian'], // 麻将类型
      numbers: Array.from({ length: 9 }, (_, i) => i + 1) // 数字1-9
    };

    // 加载底图资源
    const baseSpriteFrame = await EditorExtends.AssetDB.loadAsset(
      `db://assets/${config.basePath}.png`, 
      SpriteFrame
    ) as SpriteFrame;

    // 遍历所有牌型组合
    for (const type of config.types) {
      for (const number of config.numbers) {
        const facePath = `${config.faceDir}/mj_${type}_${number}`;
        
        // 加载牌面资源
        const faceSpriteFrame = await EditorExtends.AssetDB.loadAsset(
          `db://assets/${facePath}.png`,
          SpriteFrame
        ) as SpriteFrame;

        // 创建预制体节点
        const prefabNode = new Node(`Mahjong_${type}_${number}`);
        
        // 添加底图组件
        const baseNode = new Node('Base');
        const baseSprite = baseNode.addComponent(Sprite);
        baseSprite.spriteFrame = baseSpriteFrame;
        baseSprite.sizeMode = Sprite.SizeMode.TRIMMED;
        prefabNode.addChild(baseNode);

        // 添加牌面组件
        const faceNode = new Node('Face');
        const faceSprite = faceNode.addComponent(Sprite);
        faceSprite.spriteFrame = faceSpriteFrame;
        faceNode.setPosition(0, 10); // 微调牌面位置
        prefabNode.addChild(faceNode);

        // 生成Prefab并保存
        const prefab = new Prefab();
        prefab.data = prefabNode;
        const outputPath = `${config.outputDir}/mahjong_${type}_${number}.prefab`;
        await EditorExtends.AssetDB.createOrSaveAsset(
          prefab, 
          `db://assets/${outputPath}`
        );
      }
    }

    EditorExtends.Dialog.info('生成完成', `已创建${config.types.length * config.numbers.length}个Prefab`);
  }
}